import React from 'react'
import Info from './PokemonInfo.module.css'
const PokemonInfo = ({separatePokemon,Close}) => {
	if(!separatePokemon){
		return (
			<div  className={Info.PokemonTitle}>Click On Pokemon to see more</div>
			)
	} else{
		return (
		<div className={Info.pokemonInfo}>
		<span onClick={()=>{Close(false)}} className={Info.closeBtn}></span>
		 <div className={Info.picture}> <img src={separatePokemon.sprites.front_default} alt=""/></div>
		  <div className={Info.name}>{separatePokemon.name} <span>{'#0' + separatePokemon.id}</span></div>
		   <div className={Info.features}>
		   <table>
		   <tbody>
		   <tr><td colSpan="2">Type</td><td>{separatePokemon.types.map(type=><span key={type.slot}  className={Info.type}>{type.type.name}</span>)}</td></tr>
		    <tr><td colSpan="2">Attack</td><td>{separatePokemon.stats[1].base_stat}</td></tr>
		     <tr><td colSpan="2">Defence</td><td>{separatePokemon.stats[2].base_stat}</td></tr>
		      <tr><td colSpan="2">HP</td><td>{separatePokemon.stats[0].base_stat}</td></tr>
		       <tr><td colSpan="2">SP Attack</td><td>{separatePokemon.stats[3].base_stat}</td></tr>
		        <tr><td colSpan="2">SP Defence</td><td>{separatePokemon.stats[4].base_stat}</td></tr>
		         <tr><td colSpan="2">Speed</td><td>{separatePokemon.stats[5].base_stat}</td></tr>
		          <tr><td colSpan="2">Weight</td><td>{separatePokemon.weight}</td></tr>
		          <tr><td colSpan="2">Total Moves</td><td>{separatePokemon.moves.length}</td></tr>
		          </tbody>
		   </table> 
		   </div>
		</div>
		)
	}
}
export default PokemonInfo;