import React, {useEffect,useState} from 'react'
import PokemonCard from '../PokemonCard/PokemonCard'
import * as axios from 'axios'
import List from './Plist.module.css'
import PokemonInfo from '../PokemonInfo/PokemonInfo'
import Pokemons from '../../DAL/API'
import Preloader from '../../Preloader/Preloader'
const PokemonList = () =>{

const [state,setState] = useState([]);
const [pokemonData,setPokemonData] = useState([]);
const [next,setNex] = useState();
const [separatePokemon,setSeparatePokemon] = useState(null);
const [isTrue, setTruth] = useState(true);
const [Gloading,setGloading] = useState();
const [loading,setLoading] = useState();

const url = 'https://pokeapi.co/api/v2/pokemon';

    useEffect(()=>{
    async function fethcData()  {
     setGloading(true)
    let response = await Pokemons.getAllPokemons(url); 	
    	setState(response.results);
    	setNex(response.next);
     let pokemon = await loadingPokemon(response.results);
    setGloading(false)	  	
}

fethcData();
     },[])

 const loadingPokemon = async (data) => {
 	let _pokemonData = await Promise.all(data.map( async pokemon =>{
 		let pokemonRecord = await Pokemons.getPokemon(pokemon.url);
 		return pokemonRecord;
 	})
 	); 
      setPokemonData([...pokemonData,..._pokemonData]);
 }
 

   let LoadMore = () =>{
   
 Pokemons.getAllPokemons(next).then(data=>{		
    	setState([...state,...data.results]);
    	setNex(data.next);
    	let pokemon = loadingPokemon(data.results);	 
    	})
    }

    let SinglePokemon = (id) =>{
        setTruth(true);
         setLoading(true);
       Pokemons.getSeparatePokemon(id).then(data =>{          
             setSeparatePokemon(data);
             setLoading(false)
        })
    }

    let Close =  (isT) => {
        setTruth(isT);
    }

    if(Gloading){
      return  <Preloader/>
    }

	return (

        <div className={List.container}> 
		<div className={List.Gridcontainer}>
        <div className={List.Pokcontainer}>
		{pokemonData.map(p=><div key={p.data.id} onClick={()=>{SinglePokemon(p.data.id)}}><PokemonCard name={p.data.name} picture={p.data.sprites.front_default} types={p.data.types} /></div> )}
        </div>       
    <div>
    {
        loading 
        ? <Preloader/>
        : isTrue ? <PokemonInfo separatePokemon={separatePokemon} Close={Close}/> : null 
    }    
 </div>
 {
        loading 
        ? <Preloader/>
        :  <button  className={List.LoadBtn} onClick={()=>{
          LoadMore();
        }}>LoadMore</button>
    }
	
	</div>  
        </div>
		)
}

export default PokemonList;