import React from 'react'
import card from './Pcard.module.css'
import PokemonColor from '../../PokemonColor/PokemonColor'
const PokemonCard = ({name,picture,types}) =>{
  return (
  	<div className={card.card}>
  	<div className={card.photo}><img src={picture} alt=""/></div>
  	<div className={card.pokemonName}>{name}</div>
  	<div className={card.pokemonTypes}>{types.map(type=><span key={type.slot} style={{backgroundColor: PokemonColor[type.type.name]}}>{type.type.name}</span>)}</div>
  	</div>
  	)
}
export default PokemonCard;